/* HEADER IMAGE SECTION */
.header-block-div{
    position: relative;
    height: 55vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.header-block-div:before{
    content: "";
    background-image: url(/%E2%80%9Chttp://saigesunier.github.io/portfolio_website/static/media/cystoscope-cover.d32d05d7.png);
    background-size: cover;
    position: absolute;
    background-repeat: repeat;
    background-position: 50% 10%;
    background-size: 50%;
    background-attachment: fixed;
    opacity: 0.7;
    background-color:darkgrey;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    opacity: 0.5;
}
.project-title{
    position: relative;
    color: black;
    font-size: 10vh;
    text-align: center;
    padding-top: 1rem;
}
/* SIDE NAVBAR */
.project-nav-image {
    width: 20vh;
    box-shadow: 0px 0px 10px 1px #535353;
    margin: 1rem;
    transition: 0.25s;
  }
.project-nav-image:hover {
    opacity: 0.5;
    cursor: pointer;
}
.project-page-sideNav {
    display: flex;
    float: left;
    flex-direction: column;
    padding-top: 2rem;
    padding-right: 2rem;
    padding-left: 2rem;
}
.project-nav-title{
    font-size: 20px;
    font-weight: bold;
    
}
/* STORY SECTION */
.intro-div p{
    font-size: 18px;
    text-align: left;
    padding-bottom: 1rem;
}
/* DESIGN PROCESS SECTION */
.process-grid{
    display: grid;
    grid-gap: 1rem;
    gap: 1rem;
    grid-auto-flow: column; 
    margin: 0 0 1rem 0;
    height: 16rem;
    padding-bottom: 1rem;
}
.process-block-area {
    position: relative;
    width: 100%;
    height: 100%;
}
.flip-block-inner{
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
}
.process-block-area:hover .flip-block-inner{
    transform: rotateX(180deg);
}
.flip-block-front, .flip-block-back{
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}
.flip-block-front{
    background-color:none;
    align-items: center;
}
.process-image{
    display: block;
    max-height: 60%;
    width: auto;
    margin: auto;
}
.flip-block-back{
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
    opacity: 0.7;
    transform: rotateX(180deg);
}
.process-title {
    color: black;
    font-size: 25px;
  }
.process-description-sm {
    font-size: 14px;
    color: white;
    text-align: center;
    padding: 0px 20px;
}
.process-description-m {
    font-size: 18px;
    color: white;
    text-align: center;
    padding: 0px 20px;
}

/* DESIGN DESCRIPTION SECTION */
.final-design{
    display: flex;
    flex-direction: row;
}
.video-div{
    padding-left: 4rem;
}
