/* Design Process Grid */
.TZprocess-grid{
    display: grid;
    grid-gap: 1rem;
    gap: 1rem;
    grid-auto-flow: column; 
    margin: 0 0 1rem 0;
    height: 25rem;
}
.TZprocess-block-area {
    position: relative;
    width: 100%;
    height: 100%;
}
.flip-block-inner{
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
}
.TZprocess-block-area:hover .flip-block-inner{
    transform: rotateX(180deg);
}
.flip-block-front, .flip-block-back{
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}
.flip-block-front{
    background-color:none;
    align-items: center;
}
.TZprocess-image{
    display: block;
    max-height: 70%;
    width: auto;
    margin: auto;
}
.flip-block-back{
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
    opacity: 0.7;
    transform: rotateX(180deg);
}
.TZprocess-title {
    color: black;
    font-size: 18px;
  }
